export default "ion-toolbar {\n  --border-width: 0 0 0px;\n  --background: transparent;\n  position: absolute;\n  --opacity: 0;\n}\n\n.logo-block {\n  height: 200px;\n  overflow: hidden;\n}\n\n.logo-block ion-col {\n  text-align: center;\n  font-size: 24px;\n  color: darkblue;\n  opacity: 0.05;\n}\n\n.logo-block .logo {\n  left: 0;\n  right: 0;\n  top: 65px;\n  width: 30%;\n  max-width: 120px;\n  margin-left: auto;\n  margin-right: auto;\n  position: absolute;\n}\n\n.header-ios ion-toolbar:last-of-type {\n  --border-width: 0 0 0px;\n  --background: transparent;\n}\n\n.header-logo {\n  height: 150px;\n}\n\n.login1 {\n  margin-top: 24px;\n}\n\n.login1 ion-card {\n  box-shadow: none;\n  margin-left: 24px;\n  margin-right: 24px;\n  padding-bottom: 24px;\n}\n\n.login1 ion-button {\n  margin-top: 24px;\n}\n\n.login1 ion-fab-button {\n  margin: auto;\n  border-radius: 50%;\n  --box-shadow: none;\n  border: 0.02px solid #2798fb;\n  margin: auto;\n  --box-shadow: none;\n}\n\n.login1 ion-item {\n  --border-color: var(--ion-color-light, #fff);\n}\n\n.socila-login {\n  padding: 19px;\n  padding-top: 0;\n}\n\n.socila-login ion-button {\n  margin-top: 0;\n}\n\nion-segment {\n  border-radius: 0;\n}\n\n.login-register {\n  margin: 32px;\n  margin-top: 56px;\n}\n\n.login-register .login {\n  border: 0.55px solid var(--ion-color-light, #fff);\n  border-width: 0.55px 0.55px 0.55px 0.55px;\n  border-radius: 2px;\n}\n\n.login-register .login ion-item {\n  --border-color: var(--ion-color-light, #fff);\n}\n\n.loginBtn {\n  box-sizing: border-box;\n  position: relative;\n  /* width: 13em;  - apply for fixed size */\n  margin: 0.2em;\n  padding: 0 15px 0 46px;\n  border: none;\n  text-align: left;\n  line-height: 34px;\n  white-space: nowrap;\n  border-radius: 0.2em;\n  font-size: 16px;\n  color: #fff;\n}\n\n.loginBtn:before {\n  content: \"\";\n  box-sizing: border-box;\n  position: absolute;\n  top: 0;\n  left: 0;\n  width: 34px;\n  height: 100%;\n}\n\n.loginBtn:focus {\n  outline: none;\n}\n\n.loginBtn:active {\n  box-shadow: inset 0 0 0 32px rgba(0, 0, 0, 0.1);\n}\n\n/* Facebook */\n\n.loginBtn--facebook {\n  background-color: #4c69ba;\n  background-image: linear-gradient(#4c69ba, #3b55a0);\n  /*font-family: \"Helvetica neue\", Helvetica Neue, Helvetica, Arial, sans-serif;*/\n  text-shadow: 0 -1px 0 #354c8c;\n}\n\n.loginBtn--facebook:before {\n  border-right: #364e92 1px solid;\n  background: url(\"https://s3-us-west-2.amazonaws.com/s.cdpn.io/14082/icon_facebook.png\") 6px 6px no-repeat;\n}\n\n.loginBtn--facebook:hover,\n.loginBtn--facebook:focus {\n  background-color: #5b7bd5;\n  background-image: linear-gradient(#5b7bd5, #4864b1);\n}\n\n/* Google */\n\n.loginBtn--google {\n  /*font-family: \"Roboto\", Roboto, arial, sans-serif;*/\n  background: #dd4b39;\n}\n\n.loginBtn--google:before {\n  border-right: #bb3f30 1px solid;\n  background: url(\"https://s3-us-west-2.amazonaws.com/s.cdpn.io/14082/icon_google.png\") 6px 6px no-repeat;\n}\n\n.loginBtn--google:hover,\n.loginBtn--google:focus {\n  background: #e74b37;\n}\n/*# sourceMappingURL=data:application/json;base64, */"